<template>
    <div>
        <div>
            <el-page-header @back="goBack" content="">
            </el-page-header>
        </div>
        <template>
        <el-tabs type="border-card">
            <el-tab-pane label="">
                <div class="lookclass">
                     <el-table
                        :data="tableData"
                        style="width: 100%">
                        
                       <el-table-column
                            prop="qid"
                            label="ID"
                            width="180">
                        </el-table-column>
                        <el-table-column
                            prop="qname"
                            label=""
                            width="180">
                        </el-table-column>
                        <!-- <el-table-column
                            prop="data"
                            label=""
                            width="180">
                        </el-table-column>
                        <el-table-column
                            prop="number"
                            label="">
                        </el-table-column> -->

                        <el-table-column
                        fixed="right"
                        label=""
                        width="130">

                        <template slot-scope="scope">
                            <!-- <el-button type="text" size="small">
                                <el-link icon="el-icon-edit"></el-link>
                            </el-button> -->

                            <!-- <el-button @click="handleClick(scope.row)" type="text" size="small"> -->
                            <el-button @click="linkto(scope.row.qid)" type="text" size="small">
                                <el-link icon="el-icon-view"></el-link>
                                <!--  -->
                            </el-button>
                            
                        </template>

                        </el-table-column>
                        </el-table-column>
                    
                        </el-table>
                </div>
            </el-tab-pane>

            <el-tab-pane label="">
                
                <div class="lookclass">
                    <el-button type="primary" size="medium" @click="addquestion(uid,cid)"></el-button>
                        <el-table
                        :data="tableData"
                        style="width: 100%">
                        
                       <el-table-column
                            prop="qid"
                            label="ID"
                            width="180">
                        </el-table-column>
                        <el-table-column
                            prop="qname"
                            label=""
                            width="180">
                        </el-table-column>
                        <!-- <el-table-column
                            prop="data"
                            label=""
                            width="180">
                        </el-table-column>
                        <el-table-column
                            prop="number"
                            label="">
                        </el-table-column> -->

                        <el-table-column
                        fixed="right"
                        label=""
                        width="120">
                        <template slot-scope="scope">
                            <el-button
                            @click="delpro(scope.row.qid)"
                            type="text"
                            size="small">
                            <el-link icon="el-icon-delete"></el-link>
                            <!--  -->
                            </el-button>
                        </template>
                        </el-table-column>
                    </el-table>
                    
                </div>
            </el-tab-pane>

        </el-tabs>
        
        </template>
    </div>
</template>

<script>
    export default{
        name:'lookquestion',
        
        data() {
            return {
                uid:this.$route.query.uid,
                cid:this.$route.query.cid,

                activeName: 'one',

                proform:{
                    qid:'',
                    qname:'',
                },

                tableData: [{proform:this.proform}],

            };
        },
        methods: {
            goBack() {
                this.$router.go(-1)
            },

            async delpro(qid){
                await this.axios.post('http://10.122.215.247:8080/que/del',{qid:qid })
                // this.$router.push('')
                // this.$router.go(0)
                this.getusermsg()

            },
            
            linkto(qid){
                // this.$router.push('/checkquestion')
                this.$router.push({path:'mycheckquestion',query:{qid}})
            },

            addquestion(uid,cid){
                // this.$router.push('/addquestion')
                // this.$router.push({path:'/addquestion',query:{uid:this.uid,cid:this.cid}})
                this.$router.push({path:'addquestion',query:{uid,cid}})
            },

            async getusermsg(){
			const {data:res} = await this.axios.post('http://10.122.215.247:8080/que/get',{uid:this.uid,cid:this.cid})
			// console.log(1)
			console.log(res.data)
			this.proform=res.data
			this.tableData = this.proform
			this.$set(this.tableData,this.proform)
			},
        },

        //yourclassscope.row.uid
        created(){
            this.getusermsg()
        }
        
    }

</script>

<style scoped>
/* #mytopofform{
    float: right;
} */
.lookclass{
    height: 561px;
    overflow:auto ;
}
</style>