<template>
	<div>
		<el-card >
		  <div slot="header" class="clearfix">
		    <span></span>
		    <el-button style="float: right; padding: 3px 0" type="text" @click="add()"></el-button>
		  </div>
		
		<el-table :data="classlist" border stripe>
			<el-table-column label="id" prop="cid"></el-table-column>
			<el-table-column label="id" prop="uid"></el-table-column>
			<el-table-column label="" prop="stunum"></el-table-column>
			<el-table-column label="" prop="name"></el-table-column>
			<el-table-column label="" prop="date"></el-table-column>
			<el-table-column label="">
				<template slot-scope="scope">
					<el-tooltip effect="dark" content="" placement="top" :enterable="false">
				<el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteclass(scope.row.cid)"></el-button>
					</el-tooltip>			
					
					</template>
				
			</el-table-column>
			<el-table-column label="">
				<template slot-scope="scope">
					<el-tooltip effect="dark" content="" placement="top" :enterable="false">
				<el-button type="primary" size="mini" @click="detail(scope.row.cid)"></el-button>
					</el-tooltip>			
					
					</template>
				
			</el-table-column>
		</el-table>
		
		  
		</el-card>
		
		<el-dialog
		  title=""
		  :visible.sync="changeview"
		  width="50%">
		  <el-form  label-width="70px">
		    <el-form-item label="id" prop="cid">
		      <el-input v-model="cid"></el-input>
		    </el-form-item>
			</el-form>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="changeview = false"> </el-button>
		    <el-button type="primary" @click="addclass()"> </el-button>
		  </span>
		</el-dialog>
		
	  </div>
</template>
<script>
	export default {
	  data(){
		  return{
		   cid:null,
		   classlist:[],
		   changeview:false
		   
		  }
	  }	,
	  computed: {
	  	uid(){
			return this.$store.state.uid
		}
	  },
	  methods:{
		async addclass(){
			const {data:res} = await this.$http.post('http://10.122.215.247:8080/class/addstc',{cid:this.cid,uid:this.uid})
			if(res.msg!=="ok"){this.$message.info("")}
			this.$message.success('')
			this.changeview=false
			this.getclass()
		},
		add(){
			this.changeview=true
			
		},
	async getclass(){
			const {data:res} = await this.$http.post('http://10.122.215.247:8080/class/getcos',{uid:this.uid})
			if(res.msg!=="ok"){this.$message.info("")}
			this.classlist=res.data
		},
	async	deleteclass(cid){
			
			const result  = await this.$confirm(', ?', '', {
			       confirmButtonText: '',
			       cancelButtonText: '',
			       type: 'warning'
			     }).catch(err=>{
					return err
					})
		// confirmcancle
		    if(result!=='confirm'){
			return this.$message.info('')}
			await this.$http.post('http://10.122.215.247:8080/class/delsfc',{cid:cid,uid:this.uid})
			this.$message.success('')
			
			this.getclass()
		},
		detail(cid){
			
		 this.$store.commit('detailcid',cid)
		  this.$router.push('/questionlist')
		}
		
	  },
	  created(){
		  this.getclass()
	  }
	
	}
</script>

<style scoped>
	.el-breadcrumb{
		margin-bottom: 15px;
	}
	.el-card{
		box-shadow: 0 1px 1px rgba(0,0,0,0.15) !important;
	}
</style>
