<template>
    <div>
        <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="30%">
        <span></span>
        <el-input v-model="classname"></el-input>
        <span slot="footer" class="dialog-footer">
            <el-button @click="cancel"> </el-button>
            <el-button type="primary" @click="addclass"> </el-button>
        </span>
        </el-dialog>

        <el-tabs type="border-card">
            <el-tab-pane label="">
                <div class="youclass">
                     <el-table
                        :data="tableData"
                        style="width: 100%">
                        <el-table-column
                            prop="cid"
                            label="ID"
                            width="100">
                        </el-table-column>
                        
                       <el-table-column
                            prop="name"
                            label=""
                            width="180">
                        </el-table-column>
                        <el-table-column
                            prop="date"
                            label=""
                            width="180">
                        </el-table-column>
                        <el-table-column
                            prop="stunum"
                            label="">
                        </el-table-column>

                        <el-table-column
                        fixed="right"
                        label=""
                        width="130">

                        <template slot-scope="scope">
                            <!-- <el-button type="text" size="small">
                                <el-link icon="el-icon-edit"></el-link>
                            </el-button> -->

                            <!-- <el-button @click="handleClick(scope.row)" type="text" size="small"> -->
                            <el-button @click="linkto(scope.row.uid,scope.row.cid)" type="text" size="small">
                                 <!-- <el-button @click="linkto(111,111)" type="text" size="small"> -->
                                <el-link icon="el-icon-view"></el-link>
                                <!--  -->
                            </el-button>
                            
                        </template>

                        </el-table-column>
                        </el-table-column>
                    </el-table>
                </div>
            </el-tab-pane>

            <el-tab-pane label="">
                <div class="youclass">
                    <!-- <el-button type="primary" size="medium" @click="addclass"></el-button> -->
                    <el-button type="primary" size="medium" @click="dialogVisible = true"></el-button>
                        <el-table
                        :data="tableData"
                        style="width: 100%">

                        <el-table-column
                            prop="cid"
                            label="ID"
                            width="100">
                        </el-table-column>
                        
                       <el-table-column
                            prop="name"
                            label=""
                            width="180">
                        </el-table-column>
                        <el-table-column
                            prop="date"
                            label=""
                            width="180">
                        </el-table-column>
                        <el-table-column
                            prop="stunum"
                            label="">
                        </el-table-column>

                        <el-table-column
                        fixed="right"
                        label=""
                        width="120">
                        <template slot-scope="scope">
                            <el-button
                            @click="delclass(scope.row.name,scope.row.cid)"
                            type="text"
                            size="small">
                            <el-link icon="el-icon-delete"></el-link>
                            <!--  -->
                            </el-button>
                        </template>
                        </el-table-column>
                       </el-table>

                </div>
            </el-tab-pane>
        </el-tabs>
        <!-- <lookquestion :getuid="nextuid" 
                      :getcid="nextcid">
        </lookquestion> -->
    </div>
</template>

<script>
    export default{
        name:'yourclass',

        data() {
            return {
                // nextuid:111,//uidcid 
                // nextcid:111,

                teacheruid:this.$store.state.uid,// uid 
                classname:'',

                dialogVisible: false,

                activeName: 'one',
                count: 0,

                classform:{
                    cid:0,//ID
                    uid:0,//ID
                    stunum:0,//
                    name:'',//
                    date:'',//

                    // subject: '',
                    // data: '',
                    // number: 0
                } ,
                tableData: [{classform:this.classform}]
            };
        },
        
        computed: {
			//V-model
			username() {
				return this.$store.state.username
                // return 
			},
			uid(){
				return this.$store.state.uid
                // return 1

                // teacheruid = ?
			},
		},

        methods: {
            // addclass(){
            //     this.$router.push('addclass')
            // },

            //
            cancel(){
                this.dialogVisible = false
                this.classname=''
            },

            //
           async addclass(){
                await this.axios.post('http://10.122.215.247:8080/class/add',{name:this.classname ,uid:this.teacheruid })
                this.$router.push('yourclass')
                // this.$router.go(0)
                this.getusermsg()
                this.dialogVisible = false
                this.classname=''
                

                
            },

            //
            async delclass(cname,cid){
                // axios.post('http://10.122.215.247:8080/class/del',{name:cname  ,cid:cid })
               await this.axios.post('http://10.122.215.247:8080/class/del',{name:cname ,cid:cid })


                this.$router.push('yourclass')
                // this.$router.go(0)
                this.getusermsg()
                
            },

            // scope.row.uidID      scope.row.cidID
            linkto(uid,cid){
                // this.nextuid = uid //uidcid nextuid nextcid,
                // this.nextcid = cid 
                // this.$router.push('lookquestion')

                this.$router.push({path:'lookquestion',query:{uid,cid}})
                // console.log(nextuid)
            },

            async getusermsg(){
			// console.log(this.$store.state.uid)
			// console.log(this.$store.state.username)
			const {data:res} = await this.axios.post('http://10.122.215.247:8080/class/getcot',{uid:this.uid})
			// console.log(1)
			console.log(res.data)
			this.classform=res.data
			this.tableData = this.classform
			this.$set(this.tableData,this.classform)
			// console.log(this.list[0].username)
			// console.log(this.list[0])
			},
        },
        created(){
            this.getusermsg();
        }
    }

</script>

<style>
.youclass{
    height: 585px;
    overflow:auto ;
}
</style>