<template>
	<div>
	<!--  -->
	<el-breadcrumb separator-class="el-icon-arrow-right">
	  <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
	  <el-breadcrumb-item></el-breadcrumb-item>
	</el-breadcrumb>
	<!--  -->
	<el-card>
	  <el-table :data="list">
		  <el-table-column label="" prop="username"></el-table-column>
		  <el-table-column label="uid" prop="uid"></el-table-column>
		  <el-table-column label="" prop="exp"></el-table-column>
		<!--  <el-table-column label="" prop="exp"></el-table-column> -->
		  <el-table-column label="">
			  <template slot-scope="scope">
			  <el-button type="primary" icon="el-icon-edit" size="mini" @click="changemsg()"></el-button>
			  <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteuser()"></el-button>
			  </template>
		  </el-table-column>
	  </el-table>
	</el-card>
	<!--  -->
	<el-dialog
	  title=""
	  :visible.sync="change"
	  width="50%">
	  <el-form :model="editform" :rules="rules" ref="editformref" label-width="70px">
	    <el-form-item label="" prop="password">
	      <el-input v-model="editform.repass"></el-input>
	    </el-form-item>
		</el-form>
	  <span slot="footer" class="dialog-footer">
	    <el-button @click="change = false"> </el-button>
	    <el-button type="primary" @click="changeinfo()"> </el-button>
	  </span>
	</el-dialog>
	</div>
</template>

<script>
	import Vue from 'vue'
	export default {
		
		name:'mymsg',
		data(){
			return{
				
				//	
			//username:'',
			//uid:0,
            identity:'',
			change: false,
			// 
			editform:{
				uid:0,
				username:'',
				password:'',
				repass:'',
				exp:6
			},
			list:[{editform:this.editform}]
			,
			
			rules:{
				password:[
					{required:true,message:'',trigger:'blur'},
					{ min: 8, message: '8', trigger: 'blur' }
				
				]
			}
			}
		},
		computed: {
			//V-model
			username() {
				return this.$store.state.username; 
			},
			uid(){
				return this.$store.state.uid; 
			},
		},
		
		methods: {
		 async getusermsg(){
			console.log(this.$store.state.uid)
			console.log(this.$store.state.username)
			const {data:res} = await this.$http.post('http://10.122.215.247:8080/user/getstu',{username:this.username,uid:this.uid})
			// console.log(1)
			console.log(res)
			this.editform=res.data
			this.list[0] = this.editform
			this.$set(this.list,0,this.editform)
			console.log(this.list[0].username)
			console.log(this.list[0])
			
			
			},
			// 
			 changemsg(){
				this.change=true
			},
			changeinfo(){
				//
				this.$refs.editformref.validate(async valid=>{
					if(!valid)return
					//
				const{data:res} = await this.$http.post('http://10.122.215.247:8080/user/rpwstu',this.editform)
				})
				this.$store.commit('changepass',this.editform.repass)
				//
				this.change=false
				//
				this.getusermsg()
				//
				this.$message.success('')
			},
			
		  async	deleteuser(username){
				
				   const result  = await this.$confirm(', ?', '', {
				          confirmButtonText: '',
				          cancelButtonText: '',
				          type: 'warning'
				        }).catch(err=>{
							return err
						})
						if(result!=='confirm'){
							return this.$message.info('')}
						
			const{data:res}  =	await this.$http.post('http://10.122.215.247:8080/user/delstu',this.editform)//delet	
				this.$message.success('')
				this.$router.push('/login')
			
			
			
			
		},
	},
	created(){
		this.getusermsg();
	}
}
</script>

<style scoped>
	.el-breadcrumb{
		margin-bottom: 15px;
	}
	.el-card{
		box-shadow: 0 1px 1px rgba(0,0,0,0.15) !important;
	}
</style>
