import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
       // name
        username: '',
        // number
		password:'',
        uid:0,
		cid:0,
		qid:0
        // list
    },
	 mutations:{
	
		changepass(state,pass){
			
			state.password=pass
		},
		loguser(state,msg){
								
			state.username=msg
		},
		logpass(state,msg){
			
			state.password=msg
		},
		loguid(state,msg){
				
			state.uid=msg
		},
		detailcid(state,msg){
			state.cid=msg
		},
		detailqid(state,msg){
			state.qid=msg
		}
		
		
	}
})

export default store